"use client";
import { useGetProductsQuery } from "@/state/api";
import Header from "@/app/{components}/Header";
import { DataGrid, GridColDef } from "@mui/x-data-grid";
import { ThemeProvider } from "@mui/material/styles";
import darkTheme from "@/state/darkTheme";

const columns: GridColDef[] = [
  { field: "productId", headerName: "ID", width: 90 }, // Corrected field name
  { field: "name", headerName: "Product Name", width: 200 },
  {
    field: "price",
    headerName: "Price",
    width: 110,
    type: "number",
    // Using (value, row) as requested; this returns the price with the cedi symbol
    valueGetter: (value, row) => (row.price ? `â‚µ${row.price}` : "N/A"),
  },
  {
    field: "rating",
    headerName: "Rating",
    width: 110,
    type: "number",
    valueGetter: (value, row) => (row.rating ? row.rating : "N/A"),
  },
  {
    field: "stockQuantity", // Corrected field name
    headerName: "Stock Quantity",
    width: 110,
    type: "number",
    valueGetter: (value, row) =>
      row.stockQuantity ? row.stockQuantity : "N/A",
  },
  { field: "description", headerName: "Description", width: 200 },
];

const Inventory = () => {
  const { data: products, isError, isLoading } = useGetProductsQuery();

  if (isLoading) {
    return <div className="py-4">Loading...</div>;
  }
  if (isError || !products) {
    return (
      <div className="text-center text-red-500 py-4 font-semibold text-xl">
        Error! Failed to fetch Products
      </div>
    );
  }

  return (
    // Wrap the entire component in a ThemeProvider using your darkTheme
    <ThemeProvider theme={darkTheme}>
      <div className="flex flex-col">
        <Header name="Inventory" /> {/* Corrected from Invewntory */}
        <DataGrid
          rows={products}
          columns={columns}
          getRowId={(row) => row.productId} // Use productId as unique id for each row
          checkboxSelection
        />
      </div>
    </ThemeProvider>
  );
};

export default Inventory;
